@font-face {
  font-family: Avenir;
  font-style: normal;
  font-weight: 300;
  src: url("Avenir-Book.657c3568.ttf");
}

@font-face {
  font-family: Avenir;
  font-style: normal;
  font-weight: 800;
  src: url("Avenir-Heavy.01d9ab74.ttf");
}

:root {
  --font-color: #3d4e61;
  --border-color: #e1e7ed;
}

* {
  box-sizing: border-box;
}

body {
  font-family: Avenir, sans-serif;
}

.form {
  margin-top: 20px;
}

.form__label {
  width: 100%;
  display: flex;
}

.form__input {
  color: var(--font-color);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  outline: none;
  width: 100%;
  font-weight: 400;
  box-shadow: 0 1px 8px #3d4e611a;
}

.form__input--big {
  background: url("Search.ccd3c07b.svg") 26px / 19px no-repeat;
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
  line-height: 22px;
}

.form__input--small {
  background: url("Search.ccd3c07b.svg") 13px / 11px no-repeat;
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
  line-height: 19px;
}

.form__input:hover {
  box-shadow: 0 4px 4px #00000040;
}

.form__input:focus {
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  font-weight: 600;
}

.form__input--big:focus {
  background: url("Search.ccd3c07b.svg") 26px no-repeat, linear-gradient(#fff 0%, #f6f6f7 100%);
}

.form__input--small:focus {
  background-color: #0000;
  background-image: url("Search.ccd3c07b.svg"), linear-gradient(#fff 0%, #f6f6f7 100%);
  background-position: 13px, 0 0;
  background-repeat: no-repeat, repeat;
  background-size: 11px;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  font-weight: 900;
}

.form__input::placeholder {
  color: inherit;
}

